.home-wrapper{
    display: flex;
    letter-spacing: 2px;
    height: 100vh;
    background:url("https://www.itl.cat/pngfile/big/327-3274460_web-development.jpg") no-repeat center;
    background-size: cover;
    color: white;
    
}

.home-content{
    margin: auto 0;
}
.home-content>h2{
    font-size: 7vmin;
}
.home-content>h1{
    font-size: 10vmin;
}
.type{
    font-size: 8vmin;
}
.home-btn{
    margin: 30px 0px;
    padding: 2vmin 3vmin;
} 
button>a{
    color: white;
}

/* media query*/

@media screen and (max-width:450px) {
    .home-wrapper{
        width: 100%;
    }
    
    .home-content>h2{
        font-size: 10vmin;
    }
    .home-content>h1{
        font-size: 12vmin;
    }
    .type{
        font-size: 7vmin;
    }
    
}