.title>p{
    color: crimson;
}
.skills-main{
    display: flex;
}
.skills-left{
    width: 50%;
    text-align: justify;
}
.skills-left>:nth-child(1){
    font-size: 30px;
    padding: 20px 0;
}
.skills-left-images{
    display: flex;
    flex-wrap: wrap;
    gap: 5rem;
    padding-bottom: 30px;
}
.skills-left-images>img{
    height: 100px;
    width: 120px;
}
.skills-btn>a{
    color:white;
}
.skills-right{
    width: 50%;
    padding-top: 20px;
    margin: auto 0;
}
.info{
    font-size: 23px;
    font-weight: 600;
    display: flex;
    justify-content: space-between;
}
.info>:nth-child(1){
    color: black;
}
.line{
    height: 6px;
    width: 100%;
    background: rgba(133, 121, 121, 0.788);
    margin-bottom: 20px;
    position: relative;
    
}
.reactjs::before{
    content: '';
    position: absolute;
    width: 90%;
    height: 100%;
    background: crimson;
}
.bootstrap-5::before{
    content: '';
    position: absolute;
    width: 70%;
    height: 100%;
    background: crimson;
}
.css3::before{
    content: '';
    position: absolute;
    width: 95%;
    height: 100%;
    background: crimson;
}
.java::before{
    content: '';
    position: absolute;
    width: 85%;
    height: 100%;
    background: crimson;
}

.python::before{
    content: '';
    position: absolute;
    width: 80%;
    height: 100%;
    background: crimson;
}
.c::before{
    content: '';
    position: absolute;
    width: 95%;
    height: 100%;
    background: crimson;
}
.bars{
    margin-bottom: 30px;
}

@media screen and (max-width:450px) {
    .skills-main{
        display: flex;
        flex-direction: column;
    }
    .skills-left{
        width: 100%;
    } 
    .skills-btn{
        text-align: center;
    }
    .skills-right{
        width: 100%;
        padding: 0;
    } 
    .skills-left>:nth-child(1){
        font-size: 27px;
        padding: 20px 0;
    }
    .skills-left>:nth-child(2){
        font-size: 16px;
        padding-bottom: 20px;
    }
    .skills-right{
        padding: 50px 0;
        background: #000;
    }
    .skills-right>.bars>.info>span{
        color: white;
    }
    .skills-left-images{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: .5rem;
        padding-bottom: 30px;
    }
    .skills-left-images>img{
        height: 100px;
        width: 120px;
    }
}
