@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');

:root{
    --blue:rgb(109, 56, 255);
    --crimson:crimson;
    --black:rgb(0, 0, 0);
    --gray:#777;
}

.container{
    padding: 1rem 5rem;
}
.title{
    text-align: center;
    font-size: 30px;
    padding: 20px 0;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    font-family: 'Kanit', sans-serif;
}
.App{
    height: 100vh;
}

button{
    padding: 8px 18px;
    border-radius: 5px;
    background: var(--black);
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
}

@media screen and (max-width:500px){
    .container{
        padding: 0 1.5rem;
    }
}
@media screen and (max-width:900px){
    .container{
        padding: 0 3rem;
    }
}
