.about-main{
    display: flex;
}
.about-title>:nth-child(2){
    color: crimson;
}
.about-left>img{
    height: 400px;
    width: 350px;
    border-radius: 0 60px ; 
}
.about-left{ 
    width: 35%;
}
.about-right{
    width: 60%;
    text-align: justify;
    margin: auto 0;
}
.about-right>:nth-child(1){
    padding-bottom: 15px ;
    font-size: 35px;
}
span{
    color: crimson;
}
b{
    color: black;
}
.about-right>:nth-child(2){
    font-size:20px;
    color: rgb(56, 55, 55);
}
.about-btn{
    margin-top: 20px;
}

@media screen and (max-width:500px) {
    .about-main{
        display: flex;
        flex-direction: column;
    }
    .about-right{
        width: 100%;
    }
    button{
        margin-bottom: 30px;
    }
    img{
        margin-bottom: 30px;
    }
}
@media screen and (max-width:450px) {
    .about-left img{
        height: 250px;
        width: 250px;
        border-radius: 0 20px;
        margin: auto;
        margin-bottom: 30px;
    }
    .about-left{
        width: 100%;
        display: flex;
    }
    .about-right>:nth-child(1){
        padding-bottom: 15px ;
        font-size: 30px;
    }
    span{
        color: crimson;
    }
    .about-right>:nth-child(2){
        font-size:16px;
        color: rgb(56, 55, 55);
    }
    .about-btn{
        text-align: center;
    }
    
}