.nav-wrapper{
    display: flex;
    position: fixed;
    top: 0;
    
    background: crimson;
    
    color: white;
    z-index: 1;
    width: 100%;
    justify-content: space-between; 
    max-height: max-content;
}
.nav-left{
    display: flex;
    align-items: center;

}
.nav-left>p{
    margin-right:3rem;
    font-size: 40px;
    font-weight: 600;
}

.toggler{
    display: none;
}

.nav-right{
    display: flex;
    align-items: center;
}
.nav-right>ul{
    display: flex;
    gap: 2.5rem;
    padding-right: 4rem;
}
.nav-right>ul>li>a{
    color: white;
    font-size: 25px;
}
.nav-right>ul>li>a:hover{
    color: black;
}
.nav-btn>a{
    color: white;
}


@media screen and (max-width:900px){
    .nav-right>ul{
        gap: 2.5rem;   
    }
    .nav-btn{
        padding: 4px;
    }
    
}
@media screen and (max-width:450px){
    .nav-left>p{
        margin-right:3rem;
        font-size: 30px;
        font-weight: 600;
    }
    
    .toggler{
        display: flex;
        margin: 15px 0;
        padding: .7rem 1rem;
    }
    .nav-right{
        display: none;
    }
    #hidden{
        position: absolute;
        top: 100%;
        left: 50%;
        width: 100%;
        gap: 2rem;
        transform: translate(-50%);
        display: flex;
        flex-direction: column;
        background: #000;
        padding: 30px ;
    }
    #hidden>ul{
        display: flex;
        flex-direction: column;
        margin-left:70px;
    }
    ul>li{
        text-align: center;
    }
    .nav-right>ul>li>a:hover{
        color: white;
    }
    .nav-btn{
        background: crimson;
        padding: .6rem;
    } 
}

