.field-title>h5{
    padding-bottom: 40px;
}
section{
    background: #000;
    color: white;
}
.field-content{
    display: flex;
    gap: 50px;
}
.field-title>h5{
    color: crimson;
}
.card{
    background: rgba(49, 45, 45, 0.626);
    padding: 30px;
    margin-bottom: 60px;
    border-radius: 10px;
}
.card>:nth-child(1){
    text-align: center;
    color: crimson;
    font-size: 30px;
    padding-bottom: 20px;
}
.card>:nth-child(2){
    text-align: justify;
}
.card:hover{
    background: crimson;
    transform: scale(1.05);
    transition: all 300ms;
}
.card:hover h2{
    color: #111;
}

@media screen and (max-width:450px) {
    .field-content{
        display: flex;
        flex-wrap: wrap;
        gap: 0px;
    }
}