.title>h5{
    color: crimson;
}
.contact-main{
    display: flex;
    padding-top: 30px;
}
.contact-left{
    width: 50%;
    text-align: justify;
}
.contact-left>div>h1{
    font-size: 25px;
    padding-top: 10px;
}
.contact-left>div>p{
    font-size: 20px;
}

.contact-right{
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}
.form{
    display: flex;
    flex-direction: column;
    width: 80%;
    gap: 30px;
}
.user{
    height: 40px;
}
textarea{
    height: 120px;
}
.border{
    border: 2px solid black;
    border-radius: 5px;
    font-size: 20px;
}

.button{
    padding: 8px 18px;
    border-radius: 5px;
    background: var(--black);
    color: white;
    font-size: 20px;
    font-weight: 500;
    cursor: pointer;
    max-width: max-content;
    margin: 0 auto;
}
.button:active{
    background: white;
    color: black;
}
.alert{
    color: rgb(28, 39, 255);
    font-size: 20px;
    font-weight: 500;
    letter-spacing: 1px;
}
.contact-social{
    color: black;
}

@media screen and (max-width:450px) {
    .contact-main{
        flex-direction: column-reverse;
        gap: 30px;
    }
    .contact-left{
        width: 100%;
        padding-bottom: 30px;
    }
    .contact-right{
        width: 100%;
    }
}