html{
    scroll-behavior: smooth;
}
.arrow {
    background: rgba(14, 12, 12, 0.562);
    position: fixed;
    top: 50%;
    right: 0;
    border-radius: 50px;
    padding: 17px 20px;
}
.arrow>a{
    color: white;
    font-size: 20px;
    font-weight: 600;
}
.noarrow{
    display: none;
}

button{
    padding: 20px;
}

@media screen and (max-width:450px) {
    .arrow {
        padding: 13px 17px;
    }
}

 